.technology-blogs {
    display: flex;
    overflow-x: scroll;
    white-space: nowrap;
    border: 8px solid black;
    border-radius: 8px;
    margin: 10px 10px;
    padding: 20px;
}

.research-blogs {
    display: flex;
    overflow-x: scroll;
    white-space: nowrap;
    border: 8px solid black;
    border-radius: 8px;
    margin: 10px 10px;
    padding: 20px;
}

.blogs-title {
    padding: 15px;
}

::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}

::-webkit-scrollbar-track {
    background: #c8c4c4e1;
}

::-webkit-scrollbar-thumb {
    background: #888;
}

::-webkit-scrollbar-thumb:hover {
    background: #555;
}